import dynamic from 'next/dynamic';
import { memo } from 'react';
import { Type_RefFuncKrpano } from '@components/Krpano/types';
import { ControlbarWrapper } from './styled';

const ControlbarAction = dynamic(() => import('./Controlbar.Action'));

interface Props {
  krpanoRef: Type_RefFuncKrpano | null;
  isShow?: boolean;
  className?: string;
  onToggleShow?: () => void;
}

export const Controlbar = memo(
  ({ krpanoRef, className, isShow, onToggleShow }: Props) => {
    return (
      <ControlbarWrapper
        id="themeControlbar"
        className={`--controlbar-${isShow ? 'on' : 'off'} ${className}`}
      >
        <div className="--toggler" onClick={onToggleShow}>
          <i aria-hidden className="fal fa-bars" />
        </div>
        <ControlbarAction krpanoRef={krpanoRef} />
      </ControlbarWrapper>
    );
  },
);

export default Controlbar;
